/***/ (function(module, exports) {

	module.exports = {"data":{"post":{"html":"<p>So now that I've [changed]({% post_url /older/2015/2015-04-23-transition %}) my blogging platform, I have something to blog about: my new blogging platform!</p>\n<p>From the WordPress eXtended RSS file of the contents of my blog I found I had exported 3273 posts. Jekyll re-renders every post every time a change is made... on the default template it takes 3 minutes, but on the Lanyon template it ran over 20 minutes before I cancelled the build. That's why I started with only a few dozen posts.</p>\n<p>I then changed my archive strategy, using yearly and monthly archives similar to what I had used with WordPress instead of the pagination used in Lanyon, and lo and behold the build took only 2.5 minutes. The disadvantage is that GitHub won't run the <a href=\"https://github.com/jekyll/jekyll-archives\">jekyll-archives</a> plugin for me, so I have to render the site locally and push the rendered HTML to GitHub. I can live with that.</p>\n<p>The next question was whether Google (and everyone else) could find the old links on the new site. Jekyll is able to use the same <code>archives/:year/:month/:day/:title/</code> permalink stucture I was using before. But were all my links the same as before? Do they all still work? Well, that eXtended RSS file contains all the permalinks that WordPress had generated...</p>\n<pre><code> &#x3C;link>http://papasc.home/archives/1999/12/05/it-worked/&#x3C;/link>\n &#x3C;link>http://papasc.home/archives/1999/12/06/week-37/&#x3C;/link>\n &#x3C;link>http://papasc.home/archives/1999/12/07/ready-and-waiting/&#x3C;/link>\n &#x3C;link>http://papasc.home/archives/1999/12/09/linux-fever/&#x3C;/link>\n ...\n</code></pre>\n<p>(Yes, that's a fake URL. My hoster timed out producing the eXtended RSS file so I had to use a local install.) I then ran the links through a shell script I found on <a href=\"http://stackoverflow.com/questions/25135347/how-to-check-status-of-urls-from-text-file-using-bash-shell-script\">Stack Exchange</a>:</p>\n<pre><code> #!/bin/bash\n while read -ru 4 LINE; do\n     read -r REP &#x3C; &#x3C;(exec curl -IsS \"$LINE\" 2>&#x26;1)\n     echo \"$LINE: $REP\"\n done 4&#x3C; \"$1\"\n</code></pre>\n<p>And, indeed, all the links work. Whether the content still makes sense is another question, but PapaScott is back online in its entirety!</p>","fields":{"title":"See What Sticks","date":"30 Apr 2015","url":"https://papascott.de/2015-04-30-see-what-sticks/"}}},"pathContext":{"slug":"/2015-04-30-see-what-sticks/","prev":{"fields":{"title":"Transition","date":"23 Apr 2015","slug":"/2015-04-23-transition/"}},"next":{"fields":{"title":"Taking your Place","date":"25 May 2015","slug":"/2015-05-25-taking-your-place/"}}}}

/***/ })

